import React, { useState } from 'react'
import { QuestionIcon } from '@chakra-ui/icons'
import { Box, Text } from '@chakra-ui/react'
import styled from '@emotion/styled'
import MenuOverlay from './Overlay'

const Container = styled(Box)`
  width: 100%;
  border: none;
  display: block;
  position: fixed;
  bottom: 32px;
  right: 12px;
  z-index: 2147483647;
`
const StyledMenu = styled(Box)`
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 6px 10px;
  background-color: black;
  height: 50px;
  width: 50px;
  border-radius: 12px;
  color: white;
  cursor: pointer;
  margin-left: auto;
  transition: width 0.2s;

  &:hover {
    width: 100px;
    .bandit-help {
      height: auto;
      width: auto;
      display: block;
    }
  }

  .bandit-help {
    height: 0;
    width: 0;
    display: none;
  }
`

const MenuLauncher = () => {
  const [show, setShow] = useState(false)
  const openWidget = () => {
    setShow((show) => !show)
  }
  return (
    <>
      <Container>
        {show && <MenuOverlay />}
        <StyledMenu onClick={openWidget}>
          <Text mr={2} className="bandit-help">
            Bandit
          </Text>
          <QuestionIcon boxSize={23} />
        </StyledMenu>
      </Container>
    </>
  )
}

export default MenuLauncher
